@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .sidebar-icon {
        @apply relative flex items-center justify-center h-12 w-12 mt-2 mb-2 mx-auto shadow-lg bg-icon text-red-500 hover:bg-red-500 hover:text-white 
        transition-all duration-300 ease-linear cursor-pointer;
    }

    .sidebar-tooltip {
        @apply absolute w-auto p-2 m-2 min-w-max left-14
        rounded-md shadow-md
        text-white bg-black
        text-xs font-bold
        transition-all duration-100 scale-0 origin-left;
    }
}